import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { CommonModule, NgFor, NgIf } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core';
import { Address, DtvBoxSubscription } from '@entertainment/dtv-box-management/data-access';
import { AddressPipe } from './address.pipe';

@Component({
  selector: 'tg-entertainment-dtv-box-subscription-selector',
  standalone: true,
  imports: [CommonModule, TranslateModule, NgIf, NgFor, AddressPipe],
  templateUrl: './dtv-box-subscription-selector.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DtvBoxSubscriptionSelectorComponent {
  @Input() addresses?: Address[];
  @Input() selectedAddress: Address | undefined;
  @Input() selectedDtvSubscription: DtvBoxSubscription | undefined;
  @Output() addressChange = new EventEmitter<Address>();
  isSelectorOpen = false;

  toggleSelector(): void {
    this.isSelectorOpen = !this.isSelectorOpen;
  }

  onAddressClick(address: Address): void {
    this.selectedAddress = address;
    this.isSelectorOpen = true;
    this.addressChange.emit(address);
  }

  isActive(account: Address | null): boolean {
    return this.selectedAddress?.id === account?.id;
  }
}
